import Vue from 'vue'
import Router from 'vue-router'
import Meta from 'vue-meta'

import Gallery from './views/gallery'
import About from './views/about'
import Home from './views/home'
import './style.css'

Vue.use(Router)
Vue.use(Meta)
export default new Router({
  mode: 'history',
  routes: [
    {
      name: 'Gallery',
      path: '/gallery',
      component: Gallery,
    },
    {
      name: 'About',
      path: '/about',
      component: About,
    },
    {
      name: 'Home',
      path: '/',
      component: Home,
    },
  ],
})
